
.PreviewImage__container {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0px; 
    top: 0px; 
    width: 100%;
    height: 100%;
    z-index: 1090;
    visibility: visible;
}
/*
.PreviewImage__container.show {
    visibility: visible;
}

.PreviewImage__container.hide {
    visibility: hidden;
}
*/
.PreviewImage__overlay-background {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0px; 
    top: 0px; 
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1091;
    user-select: none;

}
/*
.PreviewImage__overlay-background.show {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.2s ease-in-out, visibility 0s linear 0s;
}

.PreviewImage__overlay-background.hide {
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.2s ease-in-out, visibility 0s linear 0.2s;
}
*/

.PreviewImage__content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    /* border: 1px solid black; */
    padding: 10px;
    border-radius: 3px;
    box-sizing: border-box;
    /*
    width: calc(100% - 60px);
    height: calc(100% - 60px);
    */
    width: 100%;
    height: 100%;
    z-index: 1092;
    cursor: pointer;
    overflow: hidden;
}
