.Projects__Project__content {
    width: 1fr;
    background-color: white;
    border-width: 0px;
    position: relative;
    pointer-events: auto;
    transform-origin: top;
    overflow: hidden; /* hide overflowing content */
    max-height: 0; /* hide the content */
    opacity: 1;
    visibility: hidden;
    transition: max-height 0.05s ease-in-out, opacity 0.05s ease-in-out, visibility 0s linear 0.05s;
  }
  
.Projects__Project__content.show {
    max-height: 7000px; /* show the content */
    opacity: 1;
    visibility: visible;
    transition: max-height 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0s linear 0s;
}

.Projects__Divider__container {
    position: relative;
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.15);
}