.Content__normal-text {
    font-size: 20px;
    /* margin: 20px, 30px; */
    margin-top: 40px;
    margin-bottom: 40px;
    margin-left: 40px;
    margin-right: 40px;
}

.Content__image-container {
    position: relative;
    margin-left: 40px;
    margin-right: 40px;
    margin-bottom: 40px; 
    margin-top: 40px;
    cursor: default;
}

.Content__image {
    width: 100%;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, 0.8);
}

.Content__image-description-container {
    position: absolute;
    top: 2px;
    right: 0px;
    padding: 14px;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 4px;
    font-size: 15px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.9);
}

.Content__primary-button {
    position: relative;
    padding: 8px;
    padding-left: 16px;
    padding-right: 16px;
    font-family: opensans-bold;
    color: #4A4A4A;
    font-size: 20px;
    background-color: #F6F6F6;
    border-radius: 4px;
    display: inline-block;
    border: solid 1px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.Content__primary-button:hover { 
    background-color: white;
}