/*

ozden/react v1.0

*/

@font-face {
    font-family: opensans;
    src: url(./font/open-sans/OpenSans-Regular.ttf);
    font-display: block;
}

@font-face {
    font-family: opensans-bold;
    src: url(./font/open-sans/OpenSans-Bold.ttf);
    font-display: block;
}

@font-face {
    font-family: raleway;
    src: url(./font/raleway/Raleway-Regular.ttf);
    font-display: block;
}

@font-face {
    font-family: raleway-bold;
    src: url(./font/raleway/Raleway-Bold.ttf);
    font-display: block;
}

.ozden__Box {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    left: auto;
    top: auto;
    right: auto;
    bottom: auto;
    overflow-x: hidden;
    overflow-y: hidden;
    background-color: transparent;
    border-radius: 0px;
    border: 0px solid rgba(0, 0, 0, 0.6);
    text-align: left;
    color: rgba(0, 0, 0, 0.8);
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    pointer-events: auto;
}

.ozden__FlexBox {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    left: auto;
    top: auto;
    right: auto;
    bottom: auto;
    overflow-x: hidden;
    overflow-y: hidden;
    flex-wrap: nowrap; /* wrap, nowrap */
    justify-content: center;
    align-items: center; /* flex-start, center, flex-end */
    align-content: center;
    background-color: transparent;
    border-radius: 0px;
    border: 0px solid rgba(0, 0, 0, 0.6);
    text-align: left;
    color: rgba(0, 0, 0, 0.8);
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    pointer-events: auto;
}

.ozden__Group {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    left: auto;
    top: auto;
    right: auto;
    bottom: auto;
    flex-wrap: nowrap; /* wrap, nowrap */
    justify-content: center;
    align-items: center; /* flex-start, center, flex-end */
    align-content: center;
    background-color: transparent;
    overflow-x: visible;
    overflow-y: visible;
    border-radius: 0px;
    border: 0px solid rgba(0, 0, 0, 0.6);
    text-align: left;
    color: rgba(0, 0, 0, 0.8);
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    pointer-events: auto;
}

.ozden__Group.row {
    flex-direction: row;
}

.ozden__Group.column {
    flex-direction: column;
}

.ozden__Label {
    position: absolute;
    display: block;
    overflow: hidden;
    left: auto;
    top: auto;
    right: auto;
    bottom: auto;
    width: auto;
    height: auto;
    font-family: opensans;
    font-size: 20px;
    text-align: left;
    color:rgba(0, 0, 0, 0.8);
    background-color: transparent;
    border: 0px solid rgba(0, 0, 0, 0.6);  
    padding: 0px;
    /* istenen genişlikten paddingi çıkartarak işlem yapıyor */
    box-sizing: border-box; 
    pointer-events: auto;
}