.Title__container {
    width: 600px;
    background-color: white;
    border-width: 0px;
    position: relative;
    pointer-events: auto;
}

.Title__title {
    width: fit-content;
    position: relative;
    font-size: 20px;
    color: rgb(20, 20, 20);
    background-color: white;
    border-width: 1px; /* 1px */
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.1);
    padding: 4px 10px;
    margin-left: 30px;
    margin-top: 40px;
    margin-bottom: 20px;
    border-radius: 6px;
    font-family: raleway;
}