.Accounts__button {
    background-color: whitesmoke;
    border-width: 0px;
    font-size: 22px;
    position: relative;
    cursor: pointer;
    pointer-events: auto;
}

.Accounts__button:hover {
    /* background-color: rgba(90, 187, 159, 0.2); */
    background-color: white;
}

.Accounts__button:active {
    /* background-color: rgba(90, 187, 159, 0.3); */
}

.Accounts__button-icon-container {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0px;
    right: 0px;
    width: fit-content;
    height: 100%;
    padding-right: 20px;
    background-color: transparent;
    height: '1fr';
}

.Accounts__button-icon {
    width: 20px;
    height: 16px;
    background-color: transparent;
}

.Accounts__Divider {
    position: relative;
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.15);
}