.OpenSource__ImageButton__container {
    position: relative;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 13px;
    transition: opacity 0.2s, background-color 0.2s, border 0.2s;
}

.OpenSource__ImageButton__container:hover {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 13px;
    /* background-color:rgba(0, 0, 0, 0.3); */
}

.OpenSource__ImageButton__img {
    height:160%;
    transition: height 0.2s, opacity 0.2s;
}

.OpenSource__ImageButton__img:hover {
    height:175%;
    opacity: 0.9;
}